@import "colors";


footer{
    background-color: $white;
    height: 20rem;
    width: 100%;
    color: $dark;
    display: grid;
    grid-template-columns: 4fr 1fr;
    padding: 2rem;

    >div{
        display: flex;
        flex-direction: column;
        justify-content:center ;
        >h1{
            font: 700 2rem $text1;
            text-transform: uppercase;
            color:$lightBlue ;
        }

        >p{
            font: 500 1.2rem $text1;
          
        }

        >h5{
            font: 300 2rem $text1;
            margin: 1rem 0;
            text-align: center;
        }

        >div{
            display: flex;
            flex-direction: column;
            align-items: center;

            a{
               color: $dark; 
               font: 900 2rem $text1;
               &:hover{
                color: $darkBlue;
               }
            }
        }
    }
}